import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { AfricleanSessionProvider } from 'src/providers/africlean-session/africlean-session.provider';
import { AfricleanAlertProvider } from 'src/providers/africlean-alert/africlean-alert.provider';
import { AfricleanObservablesProvider } from 'src/providers/africlean-observables/africlean-observables.provider';
import {AfricleanConfigProvider} from '../../../providers/africlean-config/africlean-config.provider';
import { AfricleanHttpUserProvider } from 'src/providers/africlean-http/africlean-http-user.provider';
import {StateSubscribe, UserRole} from '../../../providers/utils/commonEnum';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  public user: any;
  public role: number;
  /**
   * Test si la platforme est de type web
   */
  public isWeb: boolean;

  constructor(private navCtrl: NavController,
              private session: AfricleanSessionProvider,
              private alert: AfricleanAlertProvider,
              private config: AfricleanConfigProvider,
              private observableProvider: AfricleanObservablesProvider,
              private userHttp: AfricleanHttpUserProvider) {}

  /**
   * Initialisation de la page
   */
  ngOnInit(): void {
    // On recupÃ©re l'utilisateur connectÃ© (executÃ© lorsque la page Ã  Ã©tÃ© rafraichit)
    this.getUser();

    // DÃ©s que une nouvelle Authentication est effectuÃ©, on reactualise automatique les donnÃ©es user
    this.observableProvider.isAuth.subscribe(
      (isAuth) => {
        this.getUser();
      }
    );

    // Test la platforme
    this.isWeb = this.config.isWeb();
  }

  /**
   * RÃ©cupÃ¨re l'utilisateur courant
   */
  private async getUser() {
    this.user = await this.session.getItem('user');
    this.role = await this.session.getItem('role');
  }

  /**
   * Permet d'Ãªtre redirigÃ© sur la page de la fonctionnalitÃ© sÃ©lectionnÃ©e
   * @param page : string
   */
  public async goToPage(page: string) {
    this.isSubscribe(page);
  }

  /**
   * Teste si c'est un client et donc un abonnÃ©
   * On fait une requÃªte en nous nous basant sur le numÃ©ro (login) en utilisant l'url
   * user/client/login. La requÃªte nous renvoi l'objet user et on verifie si isSubscribe
   * a Ã©tÃ© modifiÃ© ou non.
   * @param page page
   */
  private async isSubscribe(page: string) {

    const role = await this.session.getItem('role');

    if (role === UserRole.CLIENT) {

      const login = await this.session.getItem('login');
      const body = {
        phoneNumber: login
      };

      if (page !== 'consult-abonnement' && page !== 'consult-informations') {
        const isSubscribe = this.user.isSubscribe;

        if (isSubscribe === StateSubscribe.NEW || isSubscribe === StateSubscribe.TERMINATION  ||
            isSubscribe === StateSubscribe.IN_PROGRESS_SUBSCRIB || isSubscribe === StateSubscribe.OTHER
            || isSubscribe === StateSubscribe.SUSPENDED) {

          this.onDemandToSubscribe();
          return;

        } else {
          // MAJ des variable de section
          this.session.setItem('isSubscribe', StateSubscribe.SUBSCRIBE);
          this.session.setItem('user', this.user);
          this.navCtrl.navigateForward(page);
        }

        /* this.userHttp.getClient('user/client/login', body).subscribe(
           (user) => {
           }

         );*/

      } else {
        this.navCtrl.navigateForward(page);
      }

    } else {
      this.navCtrl.navigateForward(page);
    }
  }

  /**
   * Demande de s'abonner
   */
  public onDemandToSubscribe() {
    const message = 'Pour accÃ¨der Ã  cette fonctionnalitÃ©, veuillez vous abonner.';
    const css = '';
    const buttons = [
        {
          text: 'S\'abonner',
          handler: () => {
            this.navCtrl.navigateForward('consult-abonnement');
          }
        }
    ];
    this.alert.alertMessage(message, css, buttons);
  }

}
